.content{
  position: fixed;
  /*padding: 10px 20px;*/
  /*overflow-y: scroll;*/
  background-color: #FFC299;
  height: 100%;
  width: 100%;
  /*bottom: 0;*/
}

.content > div{
  overflow-y: scroll;
  height: 100%;
  width: 100%;
  padding: 20px;
}

.content p{
  text-indent: 2em;
  text-align: left;
  margin-bottom: 1em;
}
.content ul{
  padding-left: 0;
}
.content li {
  list-style: none;
  text-align: left;
  padding-left: 0;
}

.content a{
  text-align:  left;
  /*border: 1px solid #000;*/
}

.page-control{
    padding: 2em 2em 4em;
}
.page-control button{
    margin: 0 1em;
    border-radius: 1em;
    border: none;
    width: 6em;
    height: 2em;
    font-size: 14px;
    background: #eee;
}


