.head{
  position:fixed;
  top: 0;
  width: 100%;
  height: 0em;
  overflow: hidden;
  background: rgba(9,9,9,.8);
  color: #ddd;
  text-align: left;
  padding-left: 2em;
  -webkit-transition: .3s ease-out;
  -o-transition: .3s ease-out;
  transition: .3s ease-out;
  z-index: 2;
}

.head-show{
  height: 4em;
}
.head span{
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
.head span:nth-child(1){
  width: 1em;
  height: 1em;
  border-radius: 50%;
  /*border:1px solid #ddd;*/
  line-height: 1em;
  text-align: center;
}
.head span:nth-child(2){
  left: 3.5em;
  /*color:red;*/
}



