.foot{
  height: 0em;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0,.9);
  -o-text-overflow: hidden;
     text-overflow: hidden;
  -webkit-transition: .3s ease-out;
  -o-transition: .3s ease-out;
  transition: .3s ease-out;
  z-index: 2;
}
.foot-show{
  height: 5em;
}
.foot .menu{
  padding-top: 1em;
}
.foot .setting{
  height: 0em;
  width: 100%;
  position: absolute;
  bottom: 5em;
  background: rgba(0,0,0,.9);
  overflow: hidden;
  -webkit-transition: .2s ease-out;
  -o-transition: .2s ease-out;
  transition: .2s ease-out;
}
.foot .setting-show{
  height: 8em;
}
.foot .setting > div{
  height: 50%;
  /*background: red;*/
  position: relative;
}
.foot .setting > div button,
.foot .setting > div label{


  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
.foot .setting > div label{
  color: #ddd;
  left: 2em;
}
.foot .setting > div button{
  border-style: solid;
  width: 6em;
  border-radius: 1em;
  /*background: rgba(0,0,0,.3);*/
}
.foot .setting > div button:nth-child(2){
  left: 7em;
}
.foot .setting > div button:nth-child(3){
  left: 17em;
}
.foot .btn-group-color button.btn-color{
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  border:1px solid #888;
}
.foot .btn-group-color button.btn-color:nth-child(1){
  left: 7em;
  background: #FFC299;
}
.foot .btn-group-color button.btn-color:nth-child(2){
  left: 10em;
  background: #85CFE8;
}
.foot .btn-group-color button.btn-color:nth-child(3){
  left: 13em;
  background: #9785E8;
}
.foot .btn-group-color button.btn-color:nth-child(4){
  left: 16em;
  background: #EE8AC2;
}
.foot .btn-group-color button.btn-color:nth-child(5){
  left: 19em;
  background: #000;
}


